@import url(https://fonts.googleapis.com/css?family=Dosis|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

body {
    background: #1d0b47;
    color: #e0c8ff;
    font-family: 'Dosis', sans-serif;
}

h1, h2, h4 {
    font-family: 'Work Sans', sans-serif;
    text-align: center;
}

.loader-container {
    background: #1d0b47;
    width: 200%;
    height: 200%;
    border-radius: 100%;
    z-index: 90;
    position: fixed;
    top: -50%;
    left: -50%;
}

.loader {
    position: absolute;
    top: calc(50% - 4em);
    left: calc(50% - 4em);
    width: 6em;
    height: 6em;
    background: url(/react-frontend/static/media/n.e893e435.svg) 50% 50% no-repeat;
    box-shadow: 0 0 6px #fff;
    border-radius: 50%;
    -webkit-animation: pulse 1.5s infinite;
            animation: pulse 1.5s infinite;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
  }

  @-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
		opacity: 0.7;		
	}
	50% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;	
	}	
	100% {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
		opacity: 0.7;	
	}			
}

  @keyframes pulse {
	0% {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
		opacity: 0.7;		
	}
	50% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		opacity: 1;	
	}	
	100% {
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
		opacity: 0.7;	
	}			
}
  
  .loader--hide {
    display: none;
    -webkit-transition: ease-out 2s;
    transition: ease-out 2s;
  }

.globe-info {
    position: relative;
}

.globe-title h1 {
    text-shadow: 0 0 20px rgba(255, 255, 255, 0.5);
    font-size: 32px;
    letter-spacing: 2.21px;
    color: #ffffff;
}

p {
    -webkit-margin-before: 5px;
            margin-block-start: 5px;
    -webkit-margin-after: 5px;
            margin-block-end: 5px;
}

.flex-container {
    padding: 0;
    margin: 0;
    list-style: none;
    
    -ms-box-orient: horizontal;
    display: -moz-flex;
    display: flex;
  }
  
  .row            { 
    flex-direction: row;
  }
  
  .row-reverse    { 
    flex-direction: row-reverse;
  }
  
  .column { 
    flex-direction: column; 
    float: left;
  }
  
  .column-reverse { 
    flex-direction: column-reverse; 
    float: right;
  }

#root {
    height: 100%;
    width: 100%;
}

.landing-page {
    background-image: url(/react-frontend/static/media/pattern-wave-pattern-wave-copy-mask.fd558059.svg);
    background-size: contain;
    height: 100%;
    -webkit-transition: all 5s linear;
    transition: all 5s linear;
}
.globe-home{
    background: url(/react-frontend/static/media/n.e893e435.svg) center 45% no-repeat;
    height: 100%;
    position: relative;
}

.globe-title{
    position: relative;
    top: 80vh;
    text-align: center;
}

.title-font {
    font-family: 'Work Sans', sans-serif;
    font-size: 16px;
    letter-spacing: 0.2px;
}

.left-number {
    position: absolute;
    left: 15%;
    top: 15%
}

.right-number {
    position: absolute;
    top: 15%;
    right: 15%;    
}

.left-info {
    position:absolute;
    top: 45%;
    left: 15%;
    
}

.right-info {
    position: absolute;
    top: 45%;
    right: 15%;
}

.detail-globe-container .row {
    margin-right: 5px;
    margin-left: 5px;
}

.detail-globe-container .col-4 {
    padding: 0px 5px;
}

.detail-globe {
    border-radius: 4px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(228, 197, 255, 0.15)), to(rgba(61, 15, 115, 0.2)));
    background-image: -webkit-linear-gradient(top, rgba(228, 197, 255, 0.15), rgba(61, 15, 115, 0.2));
    background-image: linear-gradient(to bottom, rgba(228, 197, 255, 0.15), rgba(61, 15, 115, 0.2));
    display: inline-block;
    padding: 10px 10px 20px;
    white-space: nowrap;
}

.left-detail-globe p{
    color: #c8a6ff;
}

.icon-box {
    background-color: #320e61;
    box-shadow: 0 0 3px #fff;
    border-radius: 3px;
    width: 61px;
    height: 61px;
    position: relative;
    float: left;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon-box img {
    position: absolute;
    width: 95%;
}


span.detail-total {
    font-size: 24px;
    letter-spacing: 0.5px;
    color: #fff;
    text-shadow: 0px 0px 6px #c789ff;
}

p.info-title {
    font-size: 14px;
    color: #fff;
    text-shadow : 0px 0px 6px #e0beff;
}

p.info-detail {
    font-size: 13px;
    letter-spacing: 0.19px;
}

p.info-date {
    font-size: 12px;
    letter-spacing: 0.2px;
    color: #825cc0;
}

.left-info > .row {
    display: inline;
    float: left;
}

.fadeOut{
    opacity:0;
    width:0;
    height:0;
    -webkit-transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s 2s;
    transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s 2s;

}
.fadeIn{
    opacity:1;
    width:100px;
    height:100px;
    -webkit-transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s 2s;
    transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s 2s;

}

.rounded-white {
    border: 1px solid #fff;
    background: transparent;
    border-radius: 20px;
    color: #fff;
    padding: 10px 30px;
    cursor: pointer;
}

.rounded-white:hover {
    background: #fff;
    color: #1d0b47;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    cursor: pointer;
}
